<template>
    <div class="user">
        <!--  -->
        <el-form ref="form" :model="form" label-width="60px" :inline="true">
          <el-form-item label="">
            <el-select v-model="form.status" placeholder="" size="mini">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.type" placeholder="" size="mini">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
              <el-input v-model="form.username" placeholder="" size="mini"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="query" size="mini"></el-button>
            <el-button @click="reset" size="mini"></el-button>
          </el-form-item>
        </el-form>
        <!--  -->
        <el-table ref="multipleTable" :header-row-style="headerrowstyle" :data="tableData3" tooltip-effect="dark" style="width: 100%" v-loading="tableLoading" @sort-change="sortchange">
          <el-table-column
              type="index"
              align="center"
              width="50">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="age"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="sex"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="phone"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="type"
            label=""
            align="center">
         	<template slot-scope="scope"> 
	          	<span v-if="scope.row.type=='0'" class="fail"></span>
	          	<span v-if="scope.row.type=='1'" class="warning paddingS"></span>
	          	<span v-if="scope.row.type=='2'" class="success paddingS"></span>
	        </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label=""
            align="center">
         		<template slot-scope="scope"> 
	          	<span v-if="scope.row.status=='2'"><span class="error-status"></span></span>
	          	<span v-if="scope.row.status=='1'"><span class="warning-status"></span></span>
	          	<span v-if="scope.row.status=='0'"><span class="success-status"></span></span>
	          </template>
          </el-table-column>
          <el-table-column
            prop="createTime"
            sortable='custom'
            label=""
            align="center"
            width="220">
          </el-table-column>
          <el-table-column label="" width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="looksee(scope.$index)" style="font-size:14px; color: #1890FF"></el-button>
              <span style="color:#ccc;margin:0 5px;">|</span>
              <el-button type="text" size="small">
                  <el-dropdown trigger="click">
                      <span class="el-dropdown-link" style="color: #1890FF">
                         <i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-if="scope.row.status == 0"><span class="occupy" @click="setStatus(scope.$index,1)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.status == 1"><span class="occupy" @click="delStatus(scope.$index,1)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.status == 0"><span class="occupy" @click="setStatus(scope.$index,2)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.status == 2"><span class="occupy" @click="delStatus(scope.$index,2)"></span></el-dropdown-item>
                        <el-dropdown-item style="color:red"><span @click="del(scope.$index)" class="occupy"></span></el-dropdown-item>
                      </el-dropdown-menu>
                  </el-dropdown>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="dibu">
            <span><span>{{ totalElements }}</span></span><span><span>{{ currentPage }}</span>/<span>{{ totalPages }}</span></span>
            <!-- current-page  -->
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="pagesize"
              background
              layout=" ->, prev, pager, next, sizes, jumper"
              :total="totalElements">
            </el-pagination>
        </div>
        <!-- -->
        <el-dialog
            title=""
            :visible.sync="lookVisible"
            width="550px"
            modal>
            <div class="look activity">
              <el-form ref="form" label-width="130px">
                <el-form-item label="">{{ look.information.name }}</el-form-item>
                <el-form-item label="">{{ look.information.sex }}</el-form-item>
                <el-form-item label="">{{ look.information.age }}</el-form-item>
                <el-form-item label="">{{ look.information.type == 0?'':look.information.type == 1?'':'' }}</el-form-item>
                <el-form-item label="">{{ look.information.status == 0?'':look.information.status == 1?'':'' }}</el-form-item>
                <el-form-item label="">{{ look.information.phone }}</el-form-item>
                <el-form-item label="">{{ look.information.email }}</el-form-item>
              </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="success" v-show="look.information.type == 0?false:true"></el-button>
                <el-button type="primary" @click="lookVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getUserList,setUserStatus} from './../../api/user'
import {users,delusers} from './../../api/api'
export default {
    data(){
        return {
            form:{},
            tableData3:[],
            tableLoading: false,
            currentPage: 0,
            pagesize: 10,
            totalElements: 0,
            totalPages: 0,
            page: 1,
            size: 10,
            sort: 'desc',
            field: 'createTime',
            lookVisible: false,
            look:{
                information:{}
            }
        }
    },
    created(){
        this.getList()
    },
    mounted(){

    },
    methods:{
        //
        async getList(){
            let le = {
                page: this.page,
                size: this.size,
                sort: this.sort,
                field: this.field,
                type: this.form.type,
                status: this.form.status,
                username: this.form.username
            }
            this.tableLoading = true
            var data = await getUserList(le)
            this.tableLoading = false
            this.tableData3 = data.data
            this.totalElements = data.totalSize/1
            this.totalPages = data.totalPage/1
            if(data.totalPage == 0){
                this.currentPage = 0
            }else{
                this.currentPage = data.page/1
            }
            // console.log(data)
        },
        // ()
        reset() {
            this.form = {}
            this.page = 1
            this.getList()
        },
        //
        query() {
          if(this.form.status == undefined && this.form.type == undefined && this.form.username == undefined){
            this.$message.error('')
            return
          }
          this.page = 1
          this.getList()
        },
        //
        zan(){

        },
        //-------------------begin---------------
        // 8/10/
        handleSizeChange (val) {
          this.size = val
          this.getList()
          // console.log(` ${val} `);
        },
        // 
        handleCurrentChange (val) {
          // console.log(`: ${val}`);
          this.currentPage = val
          this.page = val
          this.getList()
        },
        //-------------------end---------------

        //-------------------table begin-----------
        //
        headerrowstyle({row, rowIndex}){
           return {
                'background-color': '#ff6700',
                'font-size': '15px'
           }

        },
        // 
        sortchange (data){
            this.field = data.prop
            if(data.order == 'ascending'){
                this.sort = 'ASC'
            }else{
                this.sort = 'DESC'
            }
            this.page = 1
            this.getList()
        },
        // 
        async looksee(index){
            // let le = {
            //     id: this.tableData3[index].id
            // }
            // var data = await users(le)
            this.look.information = JSON.parse(JSON.stringify(this.tableData3[index]))
            this.lookVisible = true
        },
        // 
        del (index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id:this.tableData3[index].id,
                    ac_id:this.tableData3[index].ac_id
                }
                this.delUser(le)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });          
            });
        },
        async delUser(val){
            var data = await delusers(val)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        setStatus(index,type){
            var message
            if(type == 1){
                message=""
            }else{
                message=""
            }
            this.$prompt(message, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPattern: /\S/,
              inputErrorMessage: ''
            }).then(({ value }) => {
                var le = {
                    id: this.tableData3[index].id,
                    type:type,
                    val:value,
                    types:1
                }
                this.stauts(le)
            }).catch(() => {       
            });
        },
        async stauts(das){
            var data = await setUserStatus(das)
            console.log(data)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        delStatus(index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id: this.tableData3[index].id,
                    types:2
                }
                this.stauts(le)
            }).catch(() => {
         
            });
        }
        //-------------------table end-----------

    }
}
</script>
<style scoped lang="scss">
.user{
    background-color: #fff;
    // margin: 15px 25px;
    padding: 25px;
    border-radius: 10px;
}
// ----------------------
.dibu .el-pagination {
    float: right;
    margin-top: 20px;
}
.dibu span {
    font-family: 'PingFang SC Regular', 'PingFang SC';
    font-weight: 400;
    font-style: normal;
    color: rgba(0, 0, 0, 0.427450980392157);
    line-height: 60px;
}
</style>


