<template>
    <div class="taskType">
        <el-button type="primary" icon="el-icon-plus" style="margin-bottom:20px;" @click="news"></el-button>
        <!--  -->
        <el-table ref="multipleTable" :header-row-style="headerrowstyle" :data="tableData3" tooltip-effect="dark" style="width: 100%" v-loading="tableLoading">
          <el-table-column
              type="index"
              align="center"
              width="50">
          </el-table-column>
          <el-table-column
            prop="typeName"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="count"
            label=""
            align="center">
          </el-table-column>

          <el-table-column label="" width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="del(scope.$index)" style="font-size:14px; color: #1890FF"></el-button>
            </template>
          </el-table-column>
        </el-table>
    </div>
</template>
<script>
import {taskTypeList,taskTypeAddDel} from './../../api/api'
export default {
    data(){
        return {
            tableData3:[],
            tableLoading: false,
        }
    },
    created(){
        this.getList()
    },
    methods:{
        //
        headerrowstyle({row, rowIndex}){
           return {
                'background-color': '#ff6700',
                'font-size': '15px'
           }
        },
        async getList(){
            this.tableLoading = true
            this.tableData3 = await taskTypeList()
            this.tableLoading = false
            console.log(this.tableData3)
        },
        //
        news(){
            this.$prompt('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPattern: /\S/,
              inputErrorMessage: ''
            }).then(({ value }) => {
                this.newType(value)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });       
            });
        },
        async newType(data){
            let le = {
                "typeName":data
            }
            var das = await taskTypeAddDel(le)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        del(index){
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                this.delType(this.tableData3[index].typeId)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });          
            });
        },
        async delType(data){
            let le = {
                "typeId":data
            }
            var das = await taskTypeAddDel(le)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
    }
}
</script>
<style lang="scss" scoped>
.taskType{
    background-color: #fff;
    // margin: 15px 25px;
    padding: 25px;
    border-radius: 10px;
}
</style>





