<template>
    <div class="task">
        <!--  -->
        <el-form ref="form" :model="form" label-width="90px" :inline="true">
          <el-form-item label="">
            <el-select v-model="form.status" placeholder="" size="mini">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="lower" placeholder="" size="mini">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
              <el-input v-model="form.name" placeholder="" size="mini"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="query" size="mini"></el-button>
            <el-button @click="reset" size="mini"></el-button>
          </el-form-item>
        </el-form>
        <!--  -->
        <el-table ref="multipleTable" :header-row-style="headerrowstyle" :data="tableData3" tooltip-effect="dark" style="width: 100%" v-loading="tableLoading" @sort-change="sortchange">
          <el-table-column
              type="index"
              align="center"
              width="50">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="address_name"
            label=""
            align="center">
            <template slot-scope="scope"> 
	          	<span>{{scope.row.address_city}} - {{scope.row.address_name}}</span>
	        </template>
          </el-table-column>
          <el-table-column
            prop="typeName"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="lower"
            label=""
            align="center">
         	<template slot-scope="scope"> 
	          	<span v-if="scope.row.lower=='0'" class="success paddingS"></span>
	          	<span v-if="scope.row.lower=='1'" class="warning paddingS"></span>
	        </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label=""
            align="center">
         		<template slot-scope="scope"> 
                    <span v-if="scope.row.status=='0'"><span class="success-status"></span></span>
	          	    <span v-if="scope.row.status=='1'"><span class="file-status"></span></span>    	
	          </template>
          </el-table-column>
          <el-table-column
            prop="createTime"
            sortable='custom'
            label=""
            align="center"
            width="220">
          </el-table-column>
          <el-table-column label="" width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="looksee(scope.$index)" style="font-size:14px; color: #1890FF"></el-button>
              <span style="color:#ccc;margin:0 5px;">|</span>
              <el-button type="text" size="small">
                  <el-dropdown trigger="click">
                      <span class="el-dropdown-link" style="color: #1890FF">
                         <i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-if="scope.row.lower == 0"><span class="occupy" @click="setStatus(scope.$index,2)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.lower == 1"><span class="occupy" @click="delStatus(scope.$index,2)"></span></el-dropdown-item>
                        <el-dropdown-item style="color:red"><span @click="del(scope.$index)" class="occupy"></span></el-dropdown-item>
                      </el-dropdown-menu>
                  </el-dropdown>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="dibu">
            <span><span>{{ totalElements }}</span></span><span><span>{{ currentPage }}</span>/<span>{{ totalPages }}</span></span>
            <!-- current-page  -->
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="pagesize"
              background
              layout=" ->, prev, pager, next, sizes, jumper"
              :total="totalElements">
            </el-pagination>
        </div>
        <!-- -->
        <el-dialog
            title=""
            :visible.sync="lookVisible"
            width="600px"
            modal>
            <div class="look activity">
              <el-form ref="form" label-width="130px">
                <el-form-item label="">{{ look.name }}</el-form-item>
                <el-form-item label="">{{ look.typeName }}</el-form-item>
                <el-form-item label="">{{ look.address_city }}</el-form-item>
                <el-form-item label="">{{ look.address_name }}</el-form-item>
                <el-form-item label="">{{ look.number }}</el-form-item>
                <el-form-item label="">{{ look.money }}</el-form-item>
                <el-form-item label="">{{ look.cycle }}</el-form-item>
                <el-form-item label="">{{ look.begin_time }}</el-form-item>
                <el-form-item label="">{{ look.end_time }}</el-form-item>
                <el-form-item label="">{{ look.createTime }}</el-form-item>
                <el-form-item label="">{{ look.describe }}</el-form-item>
                <el-form-item label="">{{ look.requirement }}</el-form-item>
                <el-form-item label="">{{ look.Specific_address }}</el-form-item>
                <el-form-item label="">{{ look.status == 0?'':'' }}</el-form-item>
                <el-form-item label="">{{ look.lower == 0?'':'' }}</el-form-item>
              </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="lookPublisher"></el-button>
                <el-button type="primary" @click="lookVisible = false"> </el-button>
            </span>
        </el-dialog>
         <!-- -->
        <el-dialog
            title=""
            :visible.sync="publisherVisible"
            width="600px"
            modal>
            <div class="look activity">
              <el-form ref="form" label-width="130px">
                <el-form-item label="">{{ personal.name }}</el-form-item>
                <el-form-item label="">{{ personal.createTime }}</el-form-item>
                <el-form-item label="">{{ personal.sex }}</el-form-item>
                <el-form-item label="">{{ personal.age }}</el-form-item>
                <el-form-item label="">{{ personal.phone }}</el-form-item>
                <el-form-item label="">{{ personal.email}}</el-form-item>
                <el-form-item label="">{{ personal.praise}}%</el-form-item>
                <el-form-item label="">{{ personal.type == 0?'':personal.type == 1?'':''}}</el-form-item>
                <el-form-item label="">{{ personal.status == 0?'':personal.status == 1?'':''}}</el-form-item>
              </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="publisherVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getTaskList,delTask,setTaskStatus} from './../../api/task'
import {users,delusers} from './../../api/api'
import {queryPersonal} from './../../api/user'
export default {
    data(){
        return {
            form:{},
            tableData3:[],
            tableLoading: false,
            currentPage: 0,
            pagesize: 10,
            totalElements: 0,
            totalPages: 0,
            page: 1,
            size: 10,
            sort: 'desc',
            field: 'createTime',
            lower: '', //0 1 2
            lookVisible: false,
            publisherVisible: false,
            look:{},
            personal:{}
        }
    },
    created(){
        this.getList()
    },
    mounted(){

    },
    methods:{
        //
        async getList(){
            let le = {
                page: this.page,
                size: this.size,
                sort: this.sort,
                field: this.field,
                name: this.form.name
            }
            if(this.form.status == undefined){
                le.status = 2
            }else{
                le.status = this.form.status
            }
            if(this.lower == ''){
                le.lower = 2
            }else{
                le.lower = this.lower
            }
            this.tableLoading = true
            var data = await getTaskList(le)
            this.tableLoading = false
            this.tableData3 = data.data
            this.totalElements = data.totalSize/1
            this.totalPages = data.totalPage/1
            if(data.totalPage == 0){
                this.currentPage = 0
            }else{
                this.currentPage = data.page/1
            }
            console.log(data)
        },
        // ()
        reset() {
            this.form = {}
            this.lower = ''
            this.page = 1
            this.getList()
        },
        //
        query() {
          if(this.form.status == undefined && this.lower == undefined && this.form.name == undefined){
            this.$message.error('')
            return
          }
          this.page = 1
          this.getList()
        },
        //-------------------begin---------------
        // 8/10/
        handleSizeChange (val) {
          this.size = val
          this.getList()
          // console.log(` ${val} `);
        },
        // 
        handleCurrentChange (val) {
          // console.log(`: ${val}`);
          this.currentPage = val
          this.page = val
          this.getList()
        },
        //-------------------end---------------

        //-------------------table begin-----------
        //
        headerrowstyle({row, rowIndex}){
           return {
                'background-color': '#ff6700',
                'font-size': '15px'
           }

        },
        // 
        sortchange (data){
            this.field = data.prop
            if(data.order == 'ascending'){
                this.sort = 'ASC'
            }else{
                this.sort = 'DESC'
            }
            this.page = 1
            this.getList()
        },
        // 
        async looksee(index){
            // let le = {
            //     id: this.tableData3[index].id
            // }
            // var data = await users(le)
            this.look = JSON.parse(JSON.stringify(this.tableData3[index]))
            this.lookVisible = true
        },
        //
        async lookPublisher(){
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              spinner: 'el-icon-loading',
              background: 'rgba(0, 0, 0, 0.7)'
            });
            try{
                let le = {
                    id : this.look.id
                }
                var data = await queryPersonal(le)
                this.publisherVisible = true
                this.personal = data.information
                loading.close();
            }catch(e){
                loading.close();
            }
        },
        // 
        del (index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id:this.tableData3[index].id,
                }
                this.delUser(le)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });          
            });
        },
        async delUser(val){
            var data = await delTask(val)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        setStatus(index,type){
            var message
            message=""
            this.$prompt(message, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPattern: /\S/,
              inputErrorMessage: ''
            }).then(({ value }) => {
                var le = {
                    id: this.tableData3[index].id,
                    inid:this.tableData3[index].release_id,
                    val:value,
                    lower:1,
                    name:this.tableData3[index].name
                }
                this.stauts(le)
            }).catch(() => {       
            });
        },
        async stauts(das){
            var data = await setTaskStatus(das)
            console.log(data)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        delStatus(index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id: this.tableData3[index].id,
                    lower:0,
                }
                this.stauts(le)
            }).catch(() => {
         
            });
        }
        //-------------------table end-----------

    }
}
</script>
<style scoped lang="scss">
.task{
    background-color: #fff;
    // margin: 15px 25px;
    padding: 25px;
    border-radius: 10px;
}
// ----------------------
.dibu .el-pagination {
    float: right;
    margin-top: 20px;
}
.dibu span {
    font-family: 'PingFang SC Regular', 'PingFang SC';
    font-weight: 400;
    font-style: normal;
    color: rgba(0, 0, 0, 0.427450980392157);
    line-height: 60px;
}
</style>







