// axios
import axios from 'axios';
import Qs from 'qs';
import router from "../../router/router";
import qs from "qs";
import Vue from 'vue';
let v = new Vue();
// axios
var $http
// 
$http = axios.create({
	baseURL:'/she/dbs/model'
})
//
// 
$http.interceptors.request.use( config =>{
	// 
  //
//	var token = getTicket('token') ? getTicket('token'):''
//	config.headers.common['access-token'] = token
	return config;
}, function (error) {
	// 
	return Promise.reject(error);
});
// 
$http.interceptors.response.use(function (response) {
	// 
	//   
	return checkResponse(response);
}, function (error) {
	// 
	// 
	return Promise.reject(error);
});
function checkResponse(response){
  let res
	if(response.data.code == '0'){
		//
    res = JSON.parse(JSON.stringify(response.data.data))
		return Promise.resolve(res)
	}else{
    res = null
    v.$alert(response.data.message, '', {
      confirmButtonText: '',
		})
		return Promise.reject(res)
		// return response.data.data

	}
}
export default function(method, url, data = null) {
	method = method.toLowerCase();
	if(method =='post') {
		return $http.post(url,Qs.stringify(data))
	} else if(method == 'get') {
		return $http.get(url, {params: data})
	} else if(method =='delete') {
		return $http.delete(url, {params: data})
	} else if(method =='put') {
		return $http.put(url, Qs.stringify(data))
  } else if(method =='file') {
		return $http.post(url, data)
  }  
  else {
		console.error('method' +method)
		return false
	}
}


