<template>
    <div class="task">
        <!--  -->
        <div style="margin-bottom:10px;">
            <span></span>
            <el-radio-group v-model="radio3" size="medium" @change="getList">
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
            </el-radio-group>
        </div>
        <!--  -->
        <el-table ref="multipleTable" :header-row-style="headerrowstyle" :data="tableData3" tooltip-effect="dark" style="width: 100%" v-loading="tableLoading" @sort-change="sortchange">
          <el-table-column
              type="index"
              align="center"
              width="50">
          </el-table-column>
          <el-table-column
            prop="school"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="major"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="regular"
            label="/"
            align="center">
          </el-table-column>
          <el-table-column
            prop="identity_num"
            width="180px"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="pass"
            label=""
            align="center">
         		<template slot-scope="scope">
                     <span v-if="scope.row.pass=='-1'"><span class="warning-status"></span></span>
                    <span v-if="scope.row.pass=='0'"><span class="error-status"></span></span>
	          	    <span v-if="scope.row.pass=='1'"><span class="success-status"></span></span>    	
	          </template>
          </el-table-column>
          <el-table-column
            prop="createTime"
            sortable='custom'
            label=""
            align="center"
            width="220">
          </el-table-column>
          <el-table-column label="" width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="looksee(scope.$index)" style="font-size:14px; color: #1890FF"></el-button>
              <span style="color:#ccc;margin:0 5px;">|</span>
              <el-button type="text" size="small">
                  <el-dropdown trigger="click">
                      <span class="el-dropdown-link" style="color: #1890FF">
                         <i class="el-icon-arrow-down el-icon--right"></i>
                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-if="scope.row.pass == -1"><span class="occupy" @click="setPassT(scope.$index,1)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.pass == -1"><span class="occupy" @click="setPassW(scope.$index,0)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.pass == 0"><span class="occupy" @click="setPassT(scope.$index,1)"></span></el-dropdown-item>
                        <el-dropdown-item v-if="scope.row.pass == 1"><span class="occupy" @click="setPassW(scope.$index,0)"></span></el-dropdown-item>
                        <el-dropdown-item style="color:red"><span @click="del(scope.$index)" class="occupy"></span></el-dropdown-item>
                      </el-dropdown-menu>
                  </el-dropdown>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="dibu">
            <span><span>{{ totalElements }}</span></span><span><span>{{ currentPage }}</span>/<span>{{ totalPages }}</span></span>
            <!-- current-page  -->
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="pagesize"
              background
              layout=" ->, prev, pager, next, sizes, jumper"
              :total="totalElements">
            </el-pagination>
        </div>
        <!-- -->
        <el-dialog
            title=""
            :visible.sync="lookVisible"
            width="600px"
            modal>
            <div class="look activity">
              <el-form ref="form" label-width="130px">
                <el-form-item label="">{{ look.school }}</el-form-item>
                <el-form-item label="">{{ look.major }}</el-form-item>
                <el-form-item label="/">{{ look.regular }}</el-form-item>
                <el-form-item label="">{{ look.beginTime }}</el-form-item>
                <el-form-item label="">{{ look.endTime }}</el-form-item>
                <el-form-item label="">{{ look.pass == -1?'':look.pass == 1?'':'' }}</el-form-item>
                <el-form-item label=""><img :src="url+look.student_card" alt="" width="100%"></el-form-item>
                <el-form-item label="">{{ look.identity_num }}</el-form-item>
                <el-form-item label=""><img :src="url+look.identity_card_z" alt="" width="100%"></el-form-item>
                <el-form-item label=""><img :src="url+look.identity_card_f" alt="" width="100%"></el-form-item>
              </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="lookVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {users,delusers} from './../../api/api'
import {getRenList,urlData,delAuthen,setAuthenStatus} from './../../api/authentication'

export default {
    data(){
        return {
            form:{},
            tableData3:[],
            tableLoading: false,
            currentPage: 0,
            pagesize: 10,
            totalElements: 0,
            totalPages: 0,
            page: 1,
            size: 10,
            sort: 'desc',
            field: 'createTime',
            lower: '', //0 1 2
            lookVisible: false,
            look:{},
            radio3:'',
            url: ''
        }
    },
    created(){
        this.getList()
        this.url = urlData
    },
    mounted(){

    },
    methods:{
        //
        async getList(){
            let le = {
                page: this.page,
                size: this.size,
                sort: this.sort,
                field: this.field,
                type: "2"
            }
            switch(this.radio3){
                case "":
                    le.pass = 2
                    break;
                case "":
                    le.pass = -1
                    break;
                case "":
                    le.pass = 1
                    break;
                case "":
                    le.pass = 0
                    break;
            }
            this.tableLoading = true
            var data = await getRenList(le)
            this.tableLoading = false
            this.tableData3 = data.data
            this.totalElements = data.totalSize/1
            this.totalPages = data.totalPage/1
            if(data.totalPage == 0){
                this.currentPage = 0
            }else{
                this.currentPage = data.page/1
            }
            // console.log(data)
        },
        // ()
        reset() {
            this.form = {}
            this.lower = ''
            this.page = 1
            this.getList()
        },
        //
        query() {
          if(this.form.status == undefined && this.lower == undefined && this.form.name == undefined){
            this.$message.error('')
            return
          }
          this.page = 1
          this.getList()
        },
        //-------------------begin---------------
        // 8/10/
        handleSizeChange (val) {
          this.size = val
          this.getList()
          // console.log(` ${val} `);
        },
        // 
        handleCurrentChange (val) {
          // console.log(`: ${val}`);
          this.currentPage = val
          this.page = val
          this.getList()
        },
        //-------------------end---------------

        //-------------------table begin-----------
        //
        headerrowstyle({row, rowIndex}){
           return {
                'font-size': '15px'
           }

        },
        // 
        sortchange (data){
            this.field = data.prop
            if(data.order == 'ascending'){
                this.sort = 'ASC'
            }else{
                this.sort = 'DESC'
            }
            this.page = 1
            this.getList()
        },
        // 
        async looksee(index){
            // let le = {
            //     id: this.tableData3[index].id
            // }
            // var data = await users(le)
            this.look = JSON.parse(JSON.stringify(this.tableData3[index]))
            this.lookVisible = true
        },
        // 
        del (index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id:this.tableData3[index].AU_id,
                    type:"2",
                    inid:this.tableData3[index].information_id
                }
                this.dels(le)
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });          
            });
        },
        async dels(val){
            var data = await delAuthen(val)
            // console.log(data)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        setPassW(index,type){
            var message
            message=""
            this.$prompt(message, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              inputPattern: /\S/,
              inputErrorMessage: ''
            }).then(({ value }) => {
                var le = {
                    id: this.tableData3[index].AU_id,
                    inid:this.tableData3[index].information_id,
                    val:value,
                    pass:0,
                    type: 2
                }
                this.stauts(le)
            }).catch(() => {       
            });
        },
        async stauts(das){
            var data = await setAuthenStatus(das)
            this.$message({
                "message":"",
                "type":"success"
            })
            this.getList()
        },
        //
        setPassT(index){
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                var le = {
                    id: this.tableData3[index].AU_id,
                    inid:this.tableData3[index].information_id,
                    pass:1,
                    type: 2
                }
                this.stauts(le)
            }).catch(() => {
         
            });
        }
        //-------------------table end-----------

    }
}
</script>
<style scoped lang="scss">
.task{
    background-color: #fff;
    // margin: 15px 25px;
    // padding: 25px;
    border-radius: 10px;
}
// ----------------------
.dibu .el-pagination {
    float: right;
    margin-top: 20px;
}
.dibu span {
    font-family: 'PingFang SC Regular', 'PingFang SC';
    font-weight: 400;
    font-style: normal;
    color: rgba(0, 0, 0, 0.427450980392157);
    line-height: 60px;
}
</style>


